import { defineComponent } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import { Icon } from 'gw-vue-ui';
export default defineComponent({
    name: `${PREFIX}-upload`,
    mixins: [prefix],
    components: {
        GwIcon: Icon
    },
    props: {
        isNeedBrige: {
            type: Boolean,
            default: false
        },
        // 
        isUpload: {
            type: Boolean,
            default: true
        },
        // 
        isDel: {
            type: Boolean,
            default: true
        },
        disabled: {
            type: Boolean,
            default: false
        },
        accept: {
            type: String,
            default: 'image/*',
        },
        maxCount: {
            type: Number,
            default: null, //  oversize 
        },
        imgList: {
            type: Array,
            default: () => {
                return [];
            }
        },
        beforeUpload: Function // 
    },
    data() {
        return {};
    },
    methods: {
        /**
         * @function 
         */
        changeImg() {
            if (!this.isNeedBrige) {
                // input
                if (!this.disabled) {
                    this.$refs.inputer.click();
                }
            }
            else {
                // 
                console.log('brigeMethod');
                this.$emit('brigeMethod');
            }
        },
        /**
         * @function 
         */
        addImg(event) {
            var file = event.target.files[0];
            if (!this.beforeUpload) {
                // 
                return this.post(file);
            }
            const before = this.beforeUpload(file);
            if (before !== false) {
                this.post(file);
            }
        },
        /**
         * @function 
         */
        post(file) {
            var reader = new FileReader();
            var that = this;
            reader.readAsDataURL(file);
            reader.onload = function (e) {
                that.$refs.inputer.value = '';
                let item = {
                    src: e.target.result
                };
                that.$emit('successUpload', item);
            };
        },
        /**
         * @function 
         */
        deleteAction(index) {
            this.$emit('delete', index);
        }
    }
});
