import { ref, defineComponent, watch, computed } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import { routeProps } from '../../utils/useRoute';
import { useParent } from '../../utils/use/useParent';
const name = `${PREFIX}-tabs`;
export const TABS_KEY = Symbol(name);
export default defineComponent({
    name: `${PREFIX}-tab`,
    mixins: [prefix],
    props: {
        name: [Number, String],
        title: String,
        titleClass: String,
        disabled: Boolean,
        ...routeProps
    },
    setup(props, { slots }) {
        const inited = ref(false);
        const { parent, index } = useParent(name);
        if (!parent) {
            if (process.env.NODE_ENV !== 'production') {
                console.error('[Vant] <Tab> must be a child component of <Tabs>.');
            }
            return;
        }
        const getName = () => props.name ?? index.value;
        // const init = () => {
        //   inited.value = true;
        // };
        // tab
        // const isActive = () => {
        //   const active = getName() === parent.currentName.value;
        //   if (active && !inited.value) {
        //     init();
        //   }
        //   console.log('[isActive]', active)
        //   return active;
        // };
        watch(() => props.title, () => {
            parent.setLine();
            parent.scrollIntoView();
        });
        // animated, swipeable, scrollspy, lazyRender
        // return () => {
        const { animated, scrollspy } = parent.props;
        if (!slots.default && !animated) {
            return;
        }
        // const active = isActive();
        // const show = scrollspy || active;
        const show = computed(() => {
            return scrollspy || parent.currentName.value === getName();
        });
        const shouldRender = inited.value || scrollspy;
        const Content = shouldRender ? slots.default?.() : null;
        // console.log('show', show, props.title, Content)
        return { show, animated, Content };
        // }
    }
});
