import { defineComponent, computed } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import { Overlay } from 'gw-vue-ui';
// export type PopupCloseIconPosition =
//   | 'top-left'
//   | 'top-right'
//   | 'botttom-left'
//   | 'bottom-right'
export default defineComponent({
    name: `${PREFIX}-popup`,
    mixins: [prefix],
    emits: ['opened', 'closed', 'clickOverlay'],
    components: {
        GwOverlay: Overlay
    },
    props: {
        overlay: {
            type: Boolean,
            default: false
        },
        mask: {
            type: Boolean,
            default: false
        },
        overlayStyle: {
            type: Object,
            default: () => {
                return {};
            }
        },
        overlayIndex: {
            type: [Number, String],
            default: 1
        },
        teleport: {
            type: String,
            default: 'body'
        },
        show: {
            type: Boolean,
            default: false
        },
        position: {
            type: String,
            default: 'center'
        }
    },
    setup(props, { emit }) {
        // init here
        const onOpened = () => emit('opened');
        const onClosed = () => emit('closed');
        const onClickOverlay = () => emit('clickOverlay');
        const currentTransition = computed(() => {
            return props.position === 'center' ? 'gw-fade' : `gw-popup-${props.position}`;
        });
        return {
            onOpened,
            onClosed,
            onClickOverlay,
            currentTransition
        };
    }
});
