import { route, routeProps } from '../../utils/useRoute';
import { ref, watch, defineComponent, computed, reactive, nextTick, onActivated } from 'vue';
import { BORDER_TOP_BOTTOM } from '../../utils/constant';
import prefix, { PREFIX } from '../../mixins/prefix';
import { useChildren } from '../../utils/use/useChildren';
import { useRefs } from '../../utils/use/useRefs';
import { useScrollParent } from '../../utils/use/useScrollParent';
import { useEventListener, onMountedOrActivated } from '../../utils/use/useEventListener';
import { useWindowSize } from '../../utils/use/useWindowSize';
import { useExpose } from '../../utils/use/useExpose';
import { isHidden } from '../../utils/dom/style';
import { getVisibleTop } from '../../utils/dom/scroll';
import { addUnit, unitToPx } from '../../utils/format/unit';
import { isDef } from '../../utils/base';
import { scrollLeftTo, scrollTopTo } from '../utils';
import { setRootScrollTop, getElementTop } from '../../utils/dom/scroll';
import TabsTitle from './title.vue';
import TabsContent from './content.vue';
import { callInterceptor } from '../../utils/interceptor';
const name = `${PREFIX}-tabs`;
export const TABS_KEY = Symbol(name);
const pageProps = {
    active: {
        type: [Number, String],
        default: 0
    },
    type: {
        type: String,
        default: 'line'
    },
    background: {
        type: String,
        default: '#fff'
    },
    color: {
        type: String,
        default: '#f04144'
    },
    titleActiveColor: {
        type: String,
        default: ''
    },
    titleColor: {
        type: String,
        default: ''
    },
    lineWidth: [Number, String],
    lineHeight: [Number, String],
    beforeChange: Function,
    border: {
        type: Boolean,
        default: false
    },
    ellipsis: {
        type: Boolean,
        default: true
    },
    sticky: {
        type: Boolean,
        default: false
    },
    animated: {
        type: Boolean,
        default: false
    },
    scrollspy: {
        type: Boolean,
        default: false
    },
    duration: {
        type: Number,
        default: 0.3
    }
};
export default defineComponent({
    name: `${PREFIX}-tabs`,
    mixins: [prefix],
    emits: ['click', 'change', 'disabled', 'rendered', 'update:active'],
    props: {
        ...pageProps,
        ...routeProps
    },
    data() {
        return {
            BORDER_TOP_BOTTOM
        };
    },
    components: {
        TabsTitle,
        TabsContent
    },
    setup(props, { emit, slots }) {
        let lockScroll;
        let stickyFixed; // 
        const root = ref();
        const navRef = ref();
        const wrapRef = ref();
        const windowSize = useWindowSize();
        const scroller = useScrollParent(root);
        // offsetTop
        const offsetTop = 0;
        const state = reactive({
            inited: false,
            position: '',
            currentIndex: 0,
            lineStyle: {
                backgroundColor: props.color,
            },
        });
        // linkChildren setTitleRefs
        const [titleRefs, setTitleRefs] = useRefs();
        const { children, linkChildren } = useChildren(name);
        // computed offsetTop
        const offsetTopPx = computed(() => unitToPx(offsetTop));
        const scrollOffset = computed(() => {
            return 0;
        });
        // whether the nav is scrollable
        // props.swipeThreshold 
        const scrollable = computed(() => children.length > 5 || !props.ellipsis);
        const navStyle = computed(() => ({
            borderColor: props.color,
            background: props.background,
        }));
        const getTabName = (tab, index) => tab.name ?? index;
        // set currentName
        const currentName = computed(() => {
            const activeTab = children[state.currentIndex];
            if (activeTab) {
                return getTabName(activeTab, state.currentIndex);
            }
        });
        // scroll active tab into view
        const scrollIntoView = (immediate) => {
            const nav = navRef.value;
            const titles = titleRefs.value;
            if (!scrollable.value || !nav || !titles || !titles[state.currentIndex]) {
                return;
            }
            const title = titles[state.currentIndex].$el;
            const to = title.offsetLeft - (nav.offsetWidth - title.offsetWidth) / 2;
            scrollLeftTo(nav, to, immediate ? 0 : +props.duration);
        };
        // update nav bar style
        const setLine = () => {
            const shouldAnimate = state.inited;
            nextTick(() => {
                const titles = titleRefs.value;
                if (!titles ||
                    !titles[state.currentIndex] ||
                    props.type !== 'line' ||
                    isHidden(root.value)) {
                    return;
                }
                const title = titles[state.currentIndex].$el;
                const { lineWidth, lineHeight } = props;
                const left = title.offsetLeft + title.offsetWidth / 2;
                const lineStyle = {
                    width: addUnit(lineWidth),
                    backgroundColor: props.color,
                    transform: `translateX(${left}px) translateX(-50%)`,
                };
                if (shouldAnimate) {
                    lineStyle.transitionDuration = `${props.duration}s`;
                }
                if (isDef(lineHeight)) {
                    const height = addUnit(lineHeight);
                    lineStyle.height = height;
                    lineStyle.borderRadius = height;
                }
                state.lineStyle = lineStyle;
            });
        };
        const findAvailableTab = (index) => {
            const diff = index < state.currentIndex ? -1 : 1;
            while (index >= 0 && index < children.length) {
                if (!children[index].disabled) {
                    return index;
                }
                index += diff;
            }
        };
        const setCurrentIndex = (currentIndex) => {
            const newIndex = findAvailableTab(currentIndex);
            if (!isDef(newIndex)) {
                return;
            }
            const newTab = children[newIndex];
            const newName = getTabName(newTab, newIndex);
            const shouldEmitChange = state.currentIndex !== null;
            state.currentIndex = newIndex;
            if (newName !== props.active) {
                emit('update:active', newName);
                if (shouldEmitChange) {
                    emit('change', newName, newTab.title);
                }
            }
        };
        // correct the index of active tab
        const setCurrentIndexByName = (name) => {
            const matched = children.find((tab, index) => getTabName(tab, index) === name);
            const index = matched ? children.indexOf(matched) : 0;
            setCurrentIndex(index);
        };
        // tab content scroll
        const scrollToCurrentContent = (immediate = false) => {
            if (props.scrollspy) {
                const target = children[state.currentIndex].$el;
                if (target && scroller.value) {
                    const to = getElementTop(target, scroller.value) - scrollOffset.value;
                    lockScroll = true;
                    scrollTopTo(scroller.value, to, immediate ? 0 : +props.duration, () => {
                        lockScroll = false;
                    });
                }
            }
        };
        // emit event when clicked
        const onClick = (item, index) => {
            const { title, disabled } = children[index];
            const name = getTabName(children[index], index);
            if (disabled) {
                emit('disabled', name, title);
            }
            else {
                callInterceptor({
                    interceptor: props.beforeChange,
                    args: [name],
                    done: () => {
                        setCurrentIndex(index);
                        scrollToCurrentContent();
                    }
                });
                // tabtitle
                emit('click', name, title);
                route(item);
            }
        };
        const scrollTo = (name) => {
            nextTick(() => {
                setCurrentIndexByName(name);
                scrollToCurrentContent(true);
            });
        };
        const getCurrentIndexOnScroll = () => {
            for (let index = 0; index < children.length; index++) {
                const top = getVisibleTop(children[index].$el);
                if (top > scrollOffset.value) {
                    return index === 0 ? 0 : index - 1;
                }
            }
            return children.length - 1;
        };
        const onScroll = () => {
            if (props.scrollspy && !lockScroll) {
                const index = getCurrentIndexOnScroll();
                setCurrentIndex(index);
            }
        };
        watch([() => props.color, windowSize.width], setLine);
        watch(() => props.active, (value) => {
            if (value !== currentName.value) {
                setCurrentIndexByName(value);
            }
        });
        watch(() => children.length, () => {
            if (state.inited) {
                setCurrentIndexByName(props.active);
                setLine();
                nextTick(() => {
                    scrollIntoView(true);
                });
            }
        });
        watch(() => state.currentIndex, () => {
            scrollIntoView();
            setLine();
            // scroll to correct position
            if (stickyFixed && !props.scrollspy) {
                setRootScrollTop(Math.ceil(getElementTop(root.value) - offsetTopPx.value));
            }
        });
        const init = () => {
            setCurrentIndexByName(props.active);
            nextTick(() => {
                state.inited = true;
                scrollIntoView(true);
            });
        };
        const onRendered = (name, title) => emit('rendered', name, title);
        useExpose({
            resize: setLine,
            scrollTo
        });
        onActivated(setLine);
        onMountedOrActivated(init);
        useEventListener('scroll', onScroll, { target: scroller.value });
        linkChildren({
            props,
            setLine,
            onRendered,
            currentName,
            scrollIntoView
        });
        return {
            scrollable,
            navStyle,
            state,
            children,
            onClick,
            setTitleRefs,
            navRef,
            wrapRef,
            root
        };
    }
});
