import { defineComponent } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import Icon from '../../icon';
import loading from '../../loading';
export default defineComponent({
    name: `${PREFIX}-button`,
    mixins: [prefix],
    emits: ['click'],
    components: {
        GwIcon: Icon,
        GwLoading: loading
    },
    props: {
        disabled: Boolean,
        icon: String,
        loading: Boolean,
        round: Boolean,
        customStyle: String,
        type: String,
        size: {
            type: String,
            default: 'normal'
        }
    },
    setup(props, { emit }) {
        const iconSize = {
            normal: 16,
            mini: 12,
            small: 14,
            large: 24
        };
        const handleClick = () => emit('click');
        return {
            iconSize,
            handleClick
        };
    }
});
