import { defineComponent, computed } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import { preventDefault } from '../../utils/index';
export default defineComponent({
    name: `${PREFIX}-overlay`,
    mixins: [prefix],
    emits: ['click'],
    props: {
        show: {
            type: Boolean,
            default: false
        },
        mask: {
            type: Boolean,
            default: false
        },
        className: String,
        customStyle: {
            type: Object,
            default: () => {
                return {};
            }
        },
        zIndex: {
            type: [Number, String],
            default: 1
        },
        lockScroll: {
            type: Boolean,
            default: false
        },
    },
    setup(props, { emit }) {
        const onClick = (event) => {
            emit('click', event);
        };
        const preventTouchMove = (event) => {
            preventDefault(event, true);
        };
        const style = computed(() => {
            return {
                zIndex: props.zIndex,
                backgroundColor: props.mask ? 'rgba(0,0,0,0.75)' : '',
                ...props.customStyle
            };
        });
        const noop = () => { };
        return {
            onClick,
            preventTouchMove,
            style,
            noop
        };
    }
});
