import { defineComponent, ref, watch, onMounted } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
export default defineComponent({
    name: `${PREFIX}-tabs`,
    mixins: [prefix],
    emits: ['change'],
    props: {
        inited: Boolean,
        animated: Boolean,
        count: {
            type: Number,
            required: true,
        },
        currentIndex: {
            type: Number,
            required: true,
        }
    },
    setup(props, { emit, slots }) {
        const swipeRef = ref();
        const onChange = (index) => emit('change', index);
        const swipeToCurrentTab = (index) => {
            // const swipe = swipeRef.value;
            console.log('ll', index);
            // if (swipe && swipe.state.active !== index) {
            //   swipe.swipeTo(index, { immediate: !props.inited });
            // }
        };
        watch(() => props.currentIndex, swipeToCurrentTab);
        onMounted(() => {
            swipeToCurrentTab(props.currentIndex);
        });
        return { swipeRef, onChange };
    },
});
