import { ref, computed, watch, defineComponent
// getCurrentInstance
 } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import { isDef, addUnit } from '../../utils';
import { Icon } from 'gw-vue-ui';
export default defineComponent({
    name: `${PREFIX}-image`,
    components: {
        GwIcon: Icon
    },
    mixins: [prefix],
    props: {
        src: String,
        alt: String,
        fit: String,
        width: [String, Number],
        height: [String, Number],
        radius: [String, Number],
        lazyLoad: Boolean,
        iconPrefix: String,
        showError: {
            type: Boolean,
            default: true
        },
        showLoading: {
            type: Boolean,
            default: true
        },
        errorIcon: {
            type: String,
            default: 'photo-fail'
        },
        defaultIcon: {
            type: String,
            default: 'photo'
        }
    },
    setup(props, { emit, slots }) {
        const { 
        // lazyLoad,
        // iconPrefix,
        showError, showLoading, errorIcon, defaultIcon } = props;
        // loading  error 
        const loading = ref(true);
        const error = ref(false);
        const imageRef = ref();
        // const { $Lazyload } = getCurrentInstance()!.proxy as any
        // console.log($Lazyload, '$Lazyload')
        const style = computed(() => {
            const style = {};
            if (isDef(props.width)) {
                style.width = addUnit(props.width);
            }
            if (isDef(props.height)) {
                style.height = addUnit(props.height);
            }
            if (isDef(props.radius)) {
                style.overflow = 'hidden';
                style.borderRadius = addUnit(props.radius);
            }
            return style;
        });
        const attrs = {
            alt: props.alt || '',
            style: {
                objectFit: props.fit
            }
        };
        /**
         * @function watch src 
         * @param {String} src
         */
        watch(() => props.src, () => {
            error.value = false;
            loading.value = true;
        });
        /**
         * @function  loading,
         * @param { Event } event
         */
        const onLoad = (event) => {
            loading.value = false;
            emit('load', event);
        };
        /**
         * @function 
         * @param { Event } event
         */
        const onError = (event) => {
            loading.value = false;
            error.value = true;
            emit('error', event);
        };
        // TODO: loading error 
        const renderPlaceholder = () => {
            if (loading.value && showLoading) {
                return renderLoadingIcon();
            }
            if (error.value && showError) {
                return renderErrorIcon();
            }
        };
        const renderLoadingIcon = () => {
            if (slots.loading) {
                return slots.loading();
            }
        };
        const renderErrorIcon = () => {
            if (slots.error) {
                return slots.error();
            }
        };
        const phoneIcon = defaultIcon || errorIcon;
        renderPlaceholder();
        return {
            style,
            attrs,
            imageRef,
            onLoad,
            onError,
            phoneIcon
        };
    }
});
