import { defineComponent, computed } from 'vue';
import { Overlay, Popup, Icon, Loading } from 'gw-vue-ui';
import prefix, { PREFIX } from '../../mixins/prefix';
export default defineComponent({
    name: `${PREFIX}-toast`,
    mixins: [prefix],
    props: {
        show: Boolean,
        icon: String,
        iconSize: [Number, String],
        overlay: Boolean,
        message: [Number, String],
        type: {
            type: String,
            default: 'text'
        },
        duration: {
            type: Number,
            default: 2000,
        },
        position: {
            type: String,
            default: 'mid'
        },
        loadingType: String,
    },
    components: {
        Overlay,
        Popup,
        Icon,
        Loading
    },
    setup(props, { emit }) {
        // icon type
        const toastType = computed(() => {
            let type = '';
            if (props.type === 'success') {
                type = 'check';
            }
            else if (props.type === 'fail') {
                type = 'close';
            }
            return type;
        });
        return {
            toastType
        };
    }
});
