import { defineComponent, computed } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
export default defineComponent({
    name: `${PREFIX}-tab`,
    mixins: [prefix],
    props: {
        type: String,
        color: String,
        scrollable: Boolean,
        title: String,
        isActive: Boolean,
        disabled: Boolean,
        activeColor: String,
        renderTitle: Function,
        inactiveColor: String
    },
    setup(props) {
        const style = computed(() => {
            const style = {};
            const { type, color, disabled, isActive, activeColor, inactiveColor } = props;
            const isCard = type === 'card';
            const titleColor = isActive ? color : inactiveColor;
            if (titleColor) {
                style.color = titleColor;
            }
            // card theme color
            if (color && isCard) {
                style.borderColor = color;
                // set the color of the active tab
                if (!disabled) {
                    if (isActive) {
                        style.background = color;
                        style.color = activeColor;
                    }
                    else {
                        style.color = color;
                    }
                }
            }
            return style;
        });
        return { style };
    },
});
