import { defineComponent } from 'vue';
import { Popup, Loading, Button } from 'gw-vue-ui';
import prefix, { PREFIX } from '../../mixins/prefix';
export const truthProp = {
    type: Boolean,
    default: true
};
export default defineComponent({
    name: `${PREFIX}-dialog`,
    mixins: [prefix],
    components: {
        Popup,
        Loading,
        Button
    },
    props: {
        title: String,
        zIndex: {
            type: [Number, String],
            default: 2
        },
        show: Boolean,
        duration: {
            type: Number,
            default: 2000
        },
        loading: {
            type: Boolean,
            default: false
        },
        showCancelButton: truthProp,
        cancelButtonText: {
            type: String,
            default: ''
        },
        cancelButtonColor: String,
        confirmButtonText: {
            type: String,
            default: ''
        },
        confirmButtonColor: String,
        showConfirmButton: truthProp,
        closeOnClickOverlay: Boolean,
        loadingType: String,
    },
    // 
    emits: ['confirm', 'cancel', 'update:show'],
    setup(props, { emit, slots }) {
        const onCancel = () => emit('cancel');
        const onConfirm = () => emit('confirm');
        return {
            onCancel,
            onConfirm
        };
    }
});
