import { defineComponent, computed } from 'vue';
// import { isDef } from '../../utils'
import prefix, { PREFIX } from '../../mixins/prefix';
// Composition
import { useRoute, routeProps } from '../../utils/useRoute';
import { useChildren } from '../../utils/use/useChildren';
const name = `${PREFIX}-row`;
export const ROW_KEY = Symbol(name);
export const rowProps = {
    type: String,
    justify: String,
    align: String,
    gutter: [Number, String]
};
export default defineComponent({
    name: `${PREFIX}-row`,
    mixins: [prefix],
    props: {
        ...rowProps,
        ...routeProps
    },
    data() {
        return {
            route: () => { },
            children: []
        };
    },
    created() {
        this.route = useRoute();
        // console.log('created-row', this, getCurrentInstance())
    },
    setup(props, { slots }) {
        const { children, linkChildren } = useChildren(name);
        const groups = computed(() => {
            const groups = [[]];
            let totalSpan = 0;
            children.forEach((child, index) => {
                totalSpan += Number(child.span);
                if (totalSpan > 24) {
                    groups.push([index]);
                    totalSpan -= 24;
                }
                else {
                    groups[groups.length - 1].push(index);
                }
            });
            return groups;
        });
        const spaces = computed(() => {
            const gutter = Number(props.gutter);
            const spaces = [];
            if (!gutter) {
                return spaces;
            }
            groups.value.forEach((group) => {
                const averagePadding = (gutter * (group.length - 1)) / group.length;
                group.forEach((item, index) => {
                    if (index === 0) {
                        spaces.push({ right: averagePadding });
                    }
                    else {
                        const left = gutter - spaces[item - 1].right;
                        const right = averagePadding - left;
                        spaces.push({ left, right });
                    }
                });
            });
            return spaces;
        });
        linkChildren({ spaces });
    }
});
