import { defineComponent } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import { Icon } from 'gw-vue-ui';
export default defineComponent({
    name: `${PREFIX}-nav-bar`,
    mixins: [prefix],
    components: {
        GwIcon: Icon
    },
    props: {
        isBack: {
            type: Boolean,
            default: true
        },
        title: String,
        rightText: String,
        leftText: String,
        background: String // 
    },
    emits: ['click', 'back', 'right'],
    setup(props, { emit }) {
        const handleClick = () => emit('click');
        const handleBack = () => emit('back');
        const handleRight = () => emit('right');
        return {
            handleClick,
            handleBack,
            handleRight
        };
    }
});
