import { defineComponent } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import { Icon } from 'gw-vue-ui';
export default defineComponent({
    name: `${PREFIX}-search`,
    mixins: [prefix],
    components: {
        Icon
    },
    props: {
        value: [String, Number],
        placeholderText: {
            type: String,
            default: 'Search'
        },
        cancelText: {
            type: String,
            default: ''
        },
        clearable: {
            type: Boolean,
            default: true
        },
        type: {
            type: String,
            default: 'text'
        },
        maxLength: {
            type: Number,
            default: 1000
        },
        actionType: {
            type: String,
            default: 'cancel' // cancel-search-
        },
        isAutoFocus: {
            type: Boolean,
            default: false
        },
        isShowOperation: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            realText: this.value,
            txtWidth: '100%',
            offsetRight: '-40',
            visibility: 'visible',
            focused: false
        };
    },
    created() {
        if (this.value) {
            this.setStyle();
        }
        if (this.isAutoFocus) {
            this.$nextTick(() => {
                this.$refs.searchInput.focus();
            });
        }
        if (this.isShowOperation) {
            this.$nextTick(() => {
                this.offsetRight = '0';
            });
        }
    },
    computed: {
        /**
         * 
         */
        showClearIcon() {
            return this.clearable && this.value && this.focused;
        }
    },
    methods: {
        setStyle() {
            this.focused = true;
            this.txtWidth = '68px';
            this.offsetRight = '0';
            this.visibility = 'hidden';
        },
        resetStyle() {
            this.focused = false;
            this.txtWidth = '100%';
            this.offsetRight = '-40';
            this.visibility = 'visible';
        },
        getFocus() {
            this.$nextTick(() => {
                this.$refs.searchInput.focus();
            });
        },
        handleFocus(e) {
            console.log('1');
            this.txtWidth = '68px';
            this.offsetRight = '0';
            this.focused = true;
            if (this.value) {
                this.visibility = 'hidden';
            }
            this.$emit('on-focus', e);
        },
        handleBlur(e) {
            console.log('2');
            setTimeout(() => {
                this.$emit('on-blur', e);
            }, 10);
        },
        handleInput(e) {
            console.log('3');
            this.visibility = 'hidden';
            let value = e.target.value;
            this.$emit('update:value', value);
            this.$emit('on-change', value);
        },
        handleChange(e) {
            console.log('4');
            this.$emit('on-input-change', e);
        },
        handleKeypress(e) {
            console.log('5');
            this.$emit('on-keypress', e);
        },
        /**
         * @function 
         */
        handleAction() {
            if (this.actionType === 'cancel' && !this.isShowOperation) {
                this.focused = false;
                this.offsetRight = '-40';
                this.txtWidth = '100%';
                this.visibility = 'visible';
            }
            this.$emit('handleAction', this.value);
        },
        /**
         * @function 
         */
        delAction() {
            if (this.isShowOperation) {
                this.focused = false;
                this.txtWidth = '100%';
                this.visibility = 'visible';
            }
            else {
                this.resetStyle();
            }
            this.$emit('clearAction');
        }
    }
});
