import { defineComponent, computed } from 'vue';
import { isDef, addUnit } from '../../utils';
import prefix, { PREFIX } from '../../mixins/prefix';
export default defineComponent({
    name: `${PREFIX}-loading`,
    mixins: [prefix],
    props: {
        size: [Number, String],
        color: String,
        textColor: String,
        textSize: [Number, String],
        vertical: Boolean,
        type: {
            type: String,
            default: 'circular'
        }
    },
    setup(props, { slots }) {
        const list = Array(12).fill('');
        const spinnerStyle = computed(() => {
            let style = {};
            if (isDef(props.size)) {
                style = { width: props.size && addUnit(props.size), height: addUnit(props.size) };
            }
            if (isDef(props.color)) {
                style = { color: props.color };
            }
            console.log('style', style);
            return style;
        });
        const spinnerTextStyle = computed(() => {
            let style = {};
            if (isDef(props.textSize)) {
                style = { fontSize: addUnit(props.textSize) };
            }
            if (isDef(props.color) || isDef(props.textColor)) {
                style = { color: props.textColor ?? props.color };
            }
            return style;
        });
        return {
            list,
            slots,
            spinnerStyle,
            spinnerTextStyle
        };
    }
});
