import { defineComponent } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
let constTouch = [];
export default defineComponent({
    name: `${PREFIX}-image-clipping`,
    mixins: [prefix],
    props: {
        // url
        imgUrl: {
            type: String,
            default: ''
        },
        // 
        cropWidth: {
            type: Number,
            default: 260
        },
        // 
        cropHeight: {
            type: Number,
            default: 260
        },
        // 
        otherText: {
            type: String,
            default: ''
        },
        // 
        confirmText: {
            type: String,
            default: ''
        },
        //   200
        maxImgSize: {
            type: [Number, String],
            default: 2000
        },
        // 
        outputSize: {
            type: Number,
            default: 1
        },
        // 
        outputType: {
            type: String,
            default: 'png'
        },
        // 
        full: {
            type: Boolean,
            default: false
        },
        // 
        original: {
            type: Boolean,
            default: false
        },
        // 
        centerBox: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            imgs: '',
            // 
            w: 0,
            h: 0,
            // 
            scale: 1,
            // x
            x: 0,
            // y
            y: 0,
            cutTop: 0,
            cutLeft: 0,
            // 
            loading: true,
            // 
            trueWidth: 0,
            // 
            trueHeight: 0,
            // x
            moveX: 0,
            // y
            moveY: 0,
            // 
            crop: false,
            // 
            cropping: false,
            // 
            cropW: 0,
            cropH: 0,
            // 
            support: '',
            // 
            touches: [],
            touchNow: false,
            // 
            rotate: 0,
            isIos: false,
            orientation: 0,
            // 
            coe: 0.2,
            // 
            initX: 0,
            initY: 0
        };
    },
    computed: {
        passive() {
            return {
                passive: false
            };
        }
    },
    mounted() {
        this.support =
            'onwheel' in document.createElement('div') ? 'wheel' : 'DOMMouseScroll';
        var u = navigator.userAgent;
        this.isIos = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
        // imgUrl
        this.checkedImg();
    },
    methods: {
        /**
         * @function 
         */
        checkedImg() {
            if (this.imgUrl === null || this.imgUrl === '') {
                this.imgs = '';
                return;
            }
            this.loading = true;
            this.scale = 1;
            this.rotate = 0;
            const img = new Image();
            img.onload = () => {
                let width = img.width;
                let height = img.height;
                this.imgs = this.imgUrl;
                const max = this.maxImgSize;
                // 
                if (width > max) {
                    height = (height / width) * max;
                    width = max;
                }
                // 
                if (height > max) {
                    width = (width / height) * max;
                    height = max;
                }
            };
            img.onerror = () => {
                this.$emit('imgLoad', 'error');
            };
            // base64 crossOriginiOS(10.2)
            if (this.imgUrl.substr(0, 4) !== 'data') {
                img.crossOrigin = '';
            }
            img.src = this.imgUrl;
        },
        // reload 
        reload() {
            const img = new Image();
            img.onload = () => {
                //  
                // 
                // 
                this.w = parseFloat(window.getComputedStyle((this.$refs.fsCropper)).width);
                this.h = parseFloat(window.getComputedStyle((this.$refs.fsCropper)).height);
                // 
                this.trueWidth = img.width;
                this.trueHeight = img.height;
                // 
                if (!this.original) {
                    //  mode
                    if (this.trueWidth > this.w) {
                        // 
                        this.scale = this.w / this.trueWidth;
                    }
                    if (this.trueHeight * this.scale > this.h) {
                        this.scale = this.h / this.trueHeight;
                    }
                    this.scale = this.checkedMode();
                }
                else {
                    this.scale = 1;
                }
                this.$nextTick(() => {
                    this.initX = (this.w - this.trueWidth) / 2;
                    this.initY = (this.h - this.trueHeight) / 2;
                    this.loading = false;
                    // 
                    this.getCutSize();
                });
            };
            img.onerror = () => {
                // this.$emit('imgLoad', 'error')
                // this.$emit('img-load', 'error')
            };
            img.src = this.imgs;
        },
        // 
        checkedMode() {
            let scale = 1;
            if (this.trueWidth > this.w) {
                // 
                scale = this.w / this.trueWidth;
            }
            if (this.trueHeight * scale > this.h) {
                scale = this.h / this.trueHeight;
            }
            if (this.trueHeight * scale < this.cropHeight) {
                scale = this.cropHeight / this.trueHeight;
            }
            if (this.trueWidth < this.cropWidth) {
                scale = this.cropWidth / this.trueWidth;
            }
            if (this.trueHeight < this.cropHeight) {
                scale = this.cropHeight / this.trueHeight;
            }
            return scale;
        },
        /**
         * @function 
         */
        getCutSize() {
            this.cutTop = (this.h - this.cropHeight) / 2;
            this.cutLeft = (this.w - this.cropWidth) / 2;
        },
        // 
        startMove(e) {
            // e.preventDefault()
            // 
            this.moveX = (e.clientX ? e.clientX : e.touches[0].clientX) - this.x;
            this.moveY = (e.clientY ? e.clientY : e.touches[0].clientY) - this.y;
            if (e.touches) {
                window.addEventListener('touchmove', this.moveImg, { passive: false });
                window.addEventListener('touchend', this.leaveImg, { passive: false });
                if (e.touches.length === 2) {
                    // 
                    constTouch = e.touches;
                    window.addEventListener('touchmove', this.touchScale, { passive: false });
                    window.addEventListener('touchend', this.cancelTouchScale, { passive: false });
                }
            }
            else {
                window.addEventListener('mousemove', this.moveImg);
                window.addEventListener('mouseup', this.leaveImg);
            }
        },
        // 
        moveImg(e) {
            e.preventDefault();
            if (e.touches && e.touches.length === 2) {
                constTouch = e.touches;
                window.addEventListener('touchmove', this.touchScale);
                window.addEventListener('touchend', this.cancelTouchScale);
                window.removeEventListener('touchmove', this.moveImg);
                return false;
            }
            const nowX = e.clientX ? e.clientX : e.touches[0].clientX;
            const nowY = e.clientY ? e.clientY : e.touches[0].clientY;
            let changeX, changeY;
            changeX = nowX - this.moveX;
            changeY = nowY - this.moveY;
            this.$nextTick(() => {
                if (this.centerBox) {
                    const dx = (this.cutLeft - (this.w - this.trueWidth * this.scale) / 2) / this.scale;
                    const dy = (this.cutTop - (this.h - this.trueHeight * this.scale) / 2) / this.scale;
                    changeX = Math.min(changeX, dx);
                    changeX = Math.max(changeX, -dx);
                    changeY = Math.min(changeY, dy);
                    changeY = Math.max(changeY, -dy);
                }
                this.x = changeX;
                this.y = changeY;
            });
            this.x = changeX;
            this.y = changeY;
        },
        // 
        leaveImg(e) {
            window.removeEventListener('mousemove', this.moveImg);
            window.removeEventListener('touchmove', this.moveImg);
            window.removeEventListener('mouseup', this.leaveImg);
            window.removeEventListener('touchend', this.leaveImg);
        },
        // 
        touchScale(e) {
            e.preventDefault();
            let scale = this.scale;
            // 
            // 
            var oldTouch1 = {
                x: constTouch[0].clientX,
                y: constTouch[0].clientY
            };
            var newTouch1 = {
                x: e.touches[0].clientX,
                y: e.touches[0].clientY
            };
            // 
            var oldTouch2 = {
                x: constTouch[1].clientX,
                y: constTouch[1].clientY
            };
            if (e.touches.length === 2) {
                var newTouch2 = {
                    x: e.touches[1].clientX,
                    y: e.touches[1].clientY
                };
                var oldL = Math.sqrt(Math.pow(oldTouch1.x - oldTouch2.x, 2) +
                    Math.pow(oldTouch1.y - oldTouch2.y, 2));
                var newL = Math.sqrt(Math.pow(newTouch1.x - newTouch2.x, 2) +
                    Math.pow(newTouch1.y - newTouch2.y, 2));
                var cha = newL - oldL;
                //  , 
                // 1px - 0.2
                var coe = 1;
                coe =
                    coe / this.trueWidth > coe / this.trueHeight
                        ? coe / this.trueHeight
                        : coe / this.trueWidth;
                coe = coe > 0.1 ? 0.1 : coe;
                var num = coe * cha;
                if (!this.touchNow) {
                    this.touchNow = true;
                    if (cha > 0) {
                        scale += Math.abs(num);
                    }
                    else if (cha < 0) {
                        scale = scale > Math.abs(num) ? (scale -= Math.abs(num)) : scale;
                    }
                    constTouch = e.touches;
                    setTimeout(() => {
                        this.touchNow = false;
                    }, 8);
                    if (this.centerBox) {
                        if (this.trueWidth * scale < this.cropWidth || this.trueHeight * scale < this.cropHeight) {
                            return false;
                        }
                        const dx = (this.cutLeft - (this.w - this.trueWidth * scale) / 2) / scale;
                        const dy = (this.cutTop - (this.h - this.trueHeight * scale) / 2) / scale;
                        if (this.scale > scale && (dx < this.x || -dx > this.x || dy < this.y || -dy > this.y))
                            return false;
                        this.scale = scale;
                    }
                    else {
                        this.scale = scale;
                    }
                }
            }
        },
        cancelTouchScale(e) {
            window.removeEventListener('touchmove', this.touchScale);
        },
        // 
        getCropAxis() {
            const obj = {
                x1: 0,
                x2: 0,
                y1: 0,
                y2: 0
            };
            obj.x1 = this.cutLeft;
            obj.x2 = obj.x1 + this.cropWidth;
            obj.y1 = this.cutTop;
            obj.y2 = obj.y1 + this.cropHeight;
            return obj;
        },
        /**
         * @function base64
         */
        getCropChecked(cb) {
            const canvas = document.createElement('canvas');
            // let canvas = document.getElementById('canvasID')
            // canvas.toDataURL('image/png')
            const img = new Image();
            // let rotate:number = this.rotate
            img.onload = () => {
                const ctx = canvas.getContext('2d');
                const width = this.cropWidth;
                const height = this.cropHeight;
                const dx = (this.cutLeft - (this.w - this.trueWidth * this.scale) / 2) / this.scale - this.x;
                // y
                const dy = (this.cutTop - (this.h - this.trueHeight * this.scale) / 2) / this.scale - this.y;
                // 
                setCanvasSize(width, height);
                ctx.drawImage(img, dx, dy, this.cropWidth / this.scale, this.cropHeight / this.scale, 0, 0, this.cropWidth, this.cropHeight);
                cb(canvas);
            };
            // base64
            var s = this.imgUrl.substr(0, 4);
            if (s !== 'data') {
                img.crossOrigin = 'Anonymous';
            }
            img.src = this.imgs;
            function setCanvasSize(width, height) {
                canvas.width = Math.round(width);
                canvas.height = Math.round(height);
            }
        },
        /**
         * @function 
         */
        confirm() {
            this.getCropChecked((data) => {
                const urlData = data.toDataURL('image/' + this.outputType, this.outputSize);
                this.$emit('confirmAction', urlData);
            });
        },
        /**
         * @function 
         */
        cancel() {
            this.$emit('cancelAction');
        }
    },
    watch: {
        imgUrl() {
            // , 
            this.checkedImg();
        },
        imgs(val) {
            if (val === '') {
                return;
            }
            this.reload();
        }
    }
});
